<template>
    <div class="yt_home">
        <div class="yt_lb">
            <img
            :src="'../../statics/images/yt-'+(index+1)+'.jpg'"
            v-show='isShowImg==index+1'
            v-for="(item,index) in lb_list"             
            />
        </div>
        <h1>作品欣赏</h1>
        <ul>
            <li v-for="item in list" class="clear_float">
                <div class="list_left">
                    <img :src=item.img alt="">
                    <!-- <video src=""></video> -->
                </div>
                <div class="list_right">
                    <h2>{{item.title}}</h2>
                    <h4>拍摄时间:{{item.time}}</h4>
                    <h4>拍摄地点:{{item.address}}</h4>
                    <p>
                        <span>
                            {{item.title}}简介:
                        </span>
                        {{item.intro}}
                    </p>
                </div>
            </li>
        </ul>
        <p>更多</p>
    </div>
</template>

<script>
export default {
    data(){
        return {
            list:[
                {
                    title:'山村',
                    time:'2018年3月25上午',
                    address:'安徽省黄山市黄山风景区',
                    intro:`君不见黄河之水天上来，奔流到海不复回。
                            君不见高堂明镜悲白发，朝如青丝暮成雪。
                            人生得意须尽欢，莫使金樽空对月。
                            天生我材必有用，千金散尽还复来。
                            烹羊宰牛且为乐，会须一饮三百杯。
                            岑夫子，丹丘生，将进酒，杯莫停。
                            与君歌一曲，请君为我侧耳听。
                            钟鼓馔玉不足贵，但愿长醉不复醒。
                            古来圣贤皆寂寞，惟有饮者留其名。
                            陈王昔时宴平乐，斗酒十千恣欢谑。
                            主人何为言少钱，径须沽取对君酌。
                            五花马，千金裘，
                            呼儿将出换美酒，与尔同销万古愁。`,
                    img:'../../statics/images/yt-'+1+'.jpg'
                },
                {
                    title:'岛屿',
                    time:'2018年3月25上午',
                    address:'安徽省黄山市黄山风景区',
                    intro:`噫吁嚱，危乎高哉！蜀道之难，难于上青天！
                            蚕丛及鱼凫，开国何茫然！
                            尔来四万八千岁，不与秦塞通人烟。
                            西当太白有鸟道，可以横绝峨眉巅。
                            地崩山摧壮士死，然后天梯石栈相钩连。
                            上有六龙回日之高标，下有冲波逆折之回川。
                            黄鹤之飞尚不得过，猿猱欲度愁攀援。
                            青泥何盘盘，百步九折萦岩峦。
                            扪参历井仰胁息，以手抚膺坐长叹。
                            问君西游何时还？畏途巉岩不可攀。
                            但见悲鸟号古木，雄飞雌从绕林间。
                            又闻子规啼夜月，愁空山。
                            蜀道之难,难于上青天，使人听此凋朱颜！
                            连峰去天不盈尺，枯松倒挂倚绝壁。
                            飞湍瀑流争喧豗，砯崖转石万壑雷。
                            其险也如此，嗟尔远道之人胡为乎来哉！(也若此 一作：也如此)
                            剑阁峥嵘而崔嵬，一夫当关，万夫莫开。
                            所守或匪亲，化为狼与豺。
                            朝避猛虎，夕避长蛇；磨牙吮血，杀人如麻。
                            锦城虽云乐，不如早还家。
                            蜀道之难,难于上青天，侧身西望长咨嗟！`,
                    img:'../../statics/images/yt-'+2+'.jpg'
                },
                {
                    title:'山峦',
                    time:'2018年3月25上午',
                    address:'安徽省黄山市黄山风景区',
                    intro:`八月秋高风怒号，卷我屋上三重茅。
                            茅飞渡江洒江郊，高者挂罥长林梢，下者飘转沉塘坳。

                            南村群童欺我老无力，忍能对面为盗贼，公然抱茅入竹去。
                            唇焦口燥呼不得，归来倚杖自叹息。

                            俄顷风定云墨色，秋天漠漠向昏黑。
                            布衾多年冷似铁，娇儿恶卧踏里裂。
                            床头屋漏无干处，雨脚如麻未断绝。
                            自经丧乱少睡眠，长夜沾湿何由彻？

                            安得广厦千万间，大庇天下寒士俱欢颜，风雨不动安如山！
                            呜呼！何时眼前突兀见此屋，吾庐独破受冻死亦足！`,
                    img:'../../statics/images/yt-'+3+'.jpg'
                },
                {
                    title:'岛屿',
                    time:'2018年3月25上午',
                    address:'安徽省黄山市黄山风景区',
                    intro:`君不见黄河之水天上来，奔流到海不复回。
                            君不见高堂明镜悲白发，朝如青丝暮成雪。
                            人生得意须尽欢，莫使金樽空对月。
                            天生我材必有用，千金散尽还复来。
                            烹羊宰牛且为乐，会须一饮三百杯。
                            岑夫子，丹丘生，将进酒，杯莫停。
                            与君歌一曲，请君为我侧耳听。
                            钟鼓馔玉不足贵，但愿长醉不复醒。
                            古来圣贤皆寂寞，惟有饮者留其名。
                            陈王昔时宴平乐，斗酒十千恣欢谑。
                            主人何为言少钱，径须沽取对君酌。
                            五花马，千金裘，
                            呼儿将出换美酒，与尔同销万古愁。`,
                    img:'../../statics/images/yt-'+4+'.jpg'
                },
                {
                    title:'山峦',
                    time:'2018年3月25上午',
                    address:'安徽省黄山市黄山风景区',
                    intro:`君不见黄河之水天上来，奔流到海不复回。
                            君不见高堂明镜悲白发，朝如青丝暮成雪。
                            人生得意须尽欢，莫使金樽空对月。
                            天生我材必有用，千金散尽还复来。
                            烹羊宰牛且为乐，会须一饮三百杯。
                            岑夫子，丹丘生，将进酒，杯莫停。
                            与君歌一曲，请君为我侧耳听。
                            钟鼓馔玉不足贵，但愿长醉不复醒。
                            古来圣贤皆寂寞，惟有饮者留其名。
                            陈王昔时宴平乐，斗酒十千恣欢谑。
                            主人何为言少钱，径须沽取对君酌。
                            五花马，千金裘，
                            呼儿将出换美酒，与尔同销万古愁。`,
                    img:'../../statics/images/yt-'+5+'.jpg'
                }
            ],
            isShowImg:1,
            lb_list:[1,2,3,4,5]
        }
    },
    mounted(){
        setInterval(()=>{
            // console.log(this.isShowImg);
            this.isShowImg>this.lb_list.length-1?this.isShowImg=1:this.isShowImg+=1;
        },3000);
    }
}
</script>




