<template>
  <div>
    <!-- 网站头部 -->
    <header class="yt_header">
        <div class="yt_header_left">
          <img src="./assets/images/yt-logo.png" alt="">
        </div>
        <div class="yt_header_right">
            <ul class="clear_float">
                <li :class="{yt_bottom_border:isShowBorder=='contact'}">
                    <a href="javascript:void(0)" @click="linkTo('contact')">联系</a>
                </li>
                <li :class="{yt_bottom_border:isShowBorder=='about'}">
                    <a href="javascript:void(0)" @click="linkTo('about')">关于</a>
                </li>
                <li :class="{yt_bottom_border:isShowBorder=='home'}">
                    <a href="javascript:void(0)" @click="linkTo('home')">首页</a>
                </li>
            </ul>
        </div>
    </header>
    <!-- <div class="yt_bg"></div> -->
    <!-- 网站内容 -->
    <div class="yt_content">
      <router-view></router-view>
    </div>
    <!-- 网址底部 -->
    <footer class="yt_footer">
        <div>
          <ul>
            <li>拍你所拍</li>
            <li>tel:152****8542</li>
            <li>mail:136411099@qq.com</li>
          </ul>
        </div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      isShowBorder:'home'
    }
  },
  methods:{
    linkTo(val){
      if(val=='home'){
        this.$router.push({path:'/'});
        this.isShowBorder=val;
      }else if(val=='about'){
        this.$router.push({path:'/about'});
        this.isShowBorder=val;
      }else{
        this.$router.push({path:'/contact'});
        this.isShowBorder=val;
      }
    }
  }
}
</script>



